import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { TareaActions } from '../actions'
import PropTypes from 'prop-types'
import { Table, Modal, ModalHeader, ModalBody, ModalFooter, Spinner } from 'elemental'
import { Form, FormRow, Button, FormField, FormInput, Alert } from 'elemental'


const Alerta = ({ tipo, contenido }) => (
  <div style={{margin: "12px 16px -18px 16px"}}>
  <Alert type={tipo}>
    <strong><span className="fa fa-comment"/> Mensaje:</strong>
    {" " + contenido}
  </Alert>
  </div>
);


class TareaModal extends Component {
  static propTypes = {
    isOpen: PropTypes.bool.isRequired,
    onCancel: PropTypes.func.isRequired,
    proyecto: PropTypes.object.isRequired,
    target: PropTypes.object.isRequired
  }

  constructor(props) {
    super(props);
    this.state = {
      alerta: null,
      tareas: undefined,
      actualizar: false,
      crear: false,
      target: {}
    }
    this.onGuardar = this.onGuardar.bind(this);
    this.onEliminar = this.onEliminar.bind(this);
    this.onCloceModal = this.onCloceModal.bind(this);
  }

  componentWillReceiveProps(nextProps, prevState) {
    if (nextProps.isOpen) {
      const { target, actions, proyecto } = nextProps;

      if(nextProps.tareas === undefined) {
        actions.listarTarea(proyecto, target.RU, target.RS);
      }
      else {
        this.setState({ tareas:  nextProps.tareas })

        if (nextProps.tareas.deleted) {
          actions.listarTarea(proyecto, target.RU, target.RS);
        }
        if (nextProps.tareas.created) {
          this.setState({
            alerta: <Alerta tipo={"success"}
              contenido="Se asignó la Tarea"/>,
            crear: false,
            actualizar: false,
            target: {}
          });
        }
        if(nextProps.tareas.updated) {
          this.setState({
            alerta: <Alerta tipo={"success"}
              contenido="Se actualizo la tarea"/>,
          });
        }
      }
    }
    else {
      this.setState({ tareas: undefined })
    }
  }

  onEliminar(id) {
    if(window.confirm("Eliminar la tarea?")) {
      const { proyecto, actions, target } = this.props;
      actions.eliminarTarea(proyecto, target.RU, target.RS, id);

      this.setState({
        actualizar: false,
        target: {}
      });
    }
  }

  onGuardar(event) {
    event.preventDefault();
    const { proyecto, actions, target } = this.props;
    const tarea = this.state.target;

    if (tarea.id) {
      actions.actualizarTarea(proyecto, target.RU, target.RS, tarea.id, tarea);
    }
    else {
      actions.crearTarea(proyecto, target.RU, target.RS, tarea);
    }
  }

  onCloceModal() {
    const { actions, onCancel } = this.props;
    onCancel();
    actions.resetTarea();
  }

  render() {
    const { isOpen,  } = this.props;
    let { alerta, tareas, actualizar, crear, target } = this.state;

    if (tareas === undefined) return <div></div>;

    return (
      <Modal isOpen={isOpen} onKeyDown={(event) => {
          if (event.keyCode === 27) this.onCloceModal(); // 27 keyCode (ESQ)
        }}>
        <ModalHeader text="Tareas" showCloseButton onClose={this.onCloceModal}>
	        { alerta }
	      </ModalHeader>
	      {this.props.tareas.fetching ?
	        <ModalBody>
	          <div style={{ textAlign: "center", margin: 12 }}>
	            <i className="fa fa-spinner fa-pulse fa-3x fa-fw"></i>
              <span className="sr-only">Cargando...</span>
	          </div>
	        </ModalBody>
	      :
	      <ModalBody>
	      {(actualizar || crear) ?
          <Form onSubmit={this.onGuardar}>
          <FormRow>
            <FormField width="four-fifths" label="Nombre de la tarea">
              <FormInput type="text" value={target.nombre}
                onChange={(event) => {
                  this.setState({
                    target: {...target, nombre: event.target.value}
                  });
                }}
              />
            </FormField>
            <FormField width="one-fifth" label="(%) Avance">
              <FormInput type="number" value={target.avance} max="100" min="0"
                onChange={(event) => {
                  this.setState({
                    target: {...target, avance: event.target.value}
                  });
                }}
              />
            </FormField>
            <FormField width="one-half" style={{ margin: "24px 0 -12px 0" }}>
              {this.props.tareas.updating ?
                <div>
                  <Button type="primary" size="sm">
                    <Spinner type="inverted" />{" "}
		                Guardando
		              </Button>
                </div>
              :
                <div>
                  <Button type="primary" size="sm" submit>
                    <span className="fa fa-save"/>{" "}
                    Guardar
                  </Button>{" "}
                  <Button type="link" size="sm"
                    onClick={() => {
                      this.setState({
                        alerta: null,
                        crear: false,
                        actualizar: false,
                        target: {}
                      });
                    }}
                  >
                    <span className="fa fa-arrow-left"/>{" "}
                    Cancelar
                  </Button>
                </div>
              }
            </FormField>
            {actualizar ?
              <FormField width="one-half" style={{ margin: "24px 0 -12px 0" }}>
                <div style={{ float: "right" }}>
                  <Button type="danger" size="sm"
                    onClick={() => {
                      this.onEliminar(target.id);
                    }}
                  >
                    <span className="fa fa-remove"/>{" "}
                    Eliminar
                  </Button>
                </div>
              </FormField>
              : null
            }
          </FormRow>
          </Form>
	      :
          <Table>
            <thead>
              <tr>
			          <th><strong>Nombre de la tarea</strong></th>
			          <th><strong>Avance (%)</strong></th>
			          <th><strong>Acciones</strong></th>
		          </tr>
            </thead>
            <tbody>
            {tareas.data.results.map((tarea, key) =>
              <tr key={key}>
                <td>{ tarea.nombre }</td>
                <td>{ tarea.avance } %</td>
                <td>
                  <Button type="primary" size="xs"
                    onClick={() => {
                      this.setState({
                        alerta: null,
                        crear: false,
                        actualizar: true,
                        target: tarea
                      });
                    }}
                  >
                    <span className="fa fa-edit"/>
                  </Button>{" "}
                  <Button type="danger" size="xs"
                    onClick={() => {
                      this.onEliminar(tarea.id);
                    }}
                  >
                    <span className="fa fa-remove"/>
                  </Button>
                </td>
              </tr>
            )}
            </tbody>
            <tfoot>
              <tr>
                <td colSpan="3" style={{ textAlign: "center" }}>
                  <Button type="success" size="sm"
                    onClick={() => {
                      this.setState({
                        alerta: null,
                        crear: true,
                        actualizar: false,
                        target: { id: null, nombre: "", avance: 0 }
                      });
                    }}
                  >
                    <span className="fa fa-plus-circle"/>{" "}
                    Nueva Tarea
                  </Button>
                </td>
              </tr>
            </tfoot>
          </Table>
	      }
	      </ModalBody>
	      }
      </Modal>
    );
  }
}

const mapStateToProps = state => ({
  tareas: state.resource.tareas
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(TareaActions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(TareaModal)



