import { PENDING, FULFILLED, REJECTED } from 'redux-promise-middleware'


const initialState = {
  fetching: false,
  fetched: false,
  updating: false,
  updated: false,
  creating: false,
  created: false,
  error: null,
  data: {},
  permisos: {}
}

export default function generic(state = initialState, action) {
  switch (action.type) {

    case `GET_${PENDING}`: {
      return {
        ...initialState,
        fetching: true
      }
    }

    case `GET_${FULFILLED}`: {
      return {
        ...initialState,
        fetched: true,
        error: state.error,
        data: action.payload.data
      }
    }

    case `GET_${REJECTED}`: {
      return {
        ...initialState,
        fetched: true,
        data: state.data,
        error: action.payload
      }
    }

    case `POST_${PENDING}`: {
      return {
        ...initialState,
        creating: true,
        data: state.data,
        error: state.error
      }
    }

    case `POST_${FULFILLED}`: {
      return {
        ...initialState,
        created: true,
        error: state.error,
        data: action.payload.data
      }
    }

    case `POST_${REJECTED}`: {
      return {
        ...initialState,
        data: state.data,
        error: action.payload.response
      }
    }

    case `PACTH_${PENDING}`:
    case `PUT_${PENDING}`: {
      return {
        ...initialState,
        updating: true,
        data: state.data,
        error: state.error
      }
    }

    case `PACTH_${FULFILLED}`:
    case `PUT_${FULFILLED}`: {
      return {
        ...initialState,
        updated: true,
        data: action.payload.data
      }
    }

    case `PACTH_${REJECTED}`:
    case `PUT_${REJECTED}`: {
      return {
        ...initialState,
        updating: false,
        data: state.data,
        error: action.payload.response
      }
    }

    // Falta implementar OPTIONS
    case `DELETE_${PENDING}`: {
      return {
        ...initialState,
        deleting: true,
        data: state.data,
        error: state.error
      }
    }

    case `DELETE_${FULFILLED}`: {
      return {
        ...initialState,
        deleting: false,
        deleted: true,
        data: state.data,
        error: state.error
      }
    }

    case `DELETE_${REJECTED}`: {
      return {
        ...initialState,
        deleting: false,
        data: state.data,
        error: action.payload.response
      }
    }

    // Falta implementar OPTIONS
    case `OPTIONS_${REJECTED}`: {
      return {
        ...state
      }
    }

    case `OPTIONS_${PENDING}`: {
      return {
        ...state
      }
    }

    case `OPTIONS_${FULFILLED}`: {
      return {
        ...state
      }
    }

    default:
      return state;
  }
}



