import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { bindActionCreators, applyMiddleware, createStore } from 'redux'
import { connect, Provider } from 'react-redux'
// middleware
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import promiseMiddleware from 'redux-promise-middleware'

import { Spinner } from 'elemental'
import { ResourceActions } from '../actions'
import { resourceReducer } from '../reducers'
import { ProyectoHeader, TabProyecto,
  ProyectoInfo, MatrizDeConsistencia, Reportes } from '../components'


class Proyecto extends Component {
  static propTypes = {
    auth: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired
  }

  constructor(props) {
    super(props);

    let store_matriz = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    );

    let store_proyecto_info = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    );

    let store_equipo_trabajo = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    );

    this.state = {
      store_matriz: store_matriz,
      store_proyecto_info: store_proyecto_info,
      store_equipo_trabajo: store_equipo_trabajo
    }

    const { actions, match } = props;
    actions.detalle("PROYECTO", match.params.id);
  }

  render() {
    const { proyecto, auth } = this.props;

    if (proyecto === undefined) return <div></div>;

    return (
      (auth.fetching || Object.keys(proyecto.data).length === 0) ?
        <div style={{
          textAlign: "center",
          marginTop: 140,
        }}>
          <Spinner size="lg" type="inverted" />
          <br/><br/>
          <i className="fa fa-database fa-2x"
            style={{
              color: "#ffeb3b"
            }}
          />
        </div>
      : <div>
          <Provider store={ this.state.store_equipo_trabajo }>
            <ProyectoHeader username={auth.data.username} proyecto={proyecto} />
          </Provider>
          <TabProyecto default={1}>
            <TabProyecto.Header>
              <TabProyecto.Link id={1}>
                Información
              </TabProyecto.Link>
              <TabProyecto.Link id={2}>
                Matriz
              </TabProyecto.Link>
              <TabProyecto.Link id={3}>
                Reportes
              </TabProyecto.Link>
            </TabProyecto.Header>
            <TabProyecto.Body>
              <TabProyecto.BodyItem id={1}>
                <Provider store={ this.state.store_proyecto_info }>
                  <ProyectoInfo id={proyecto.data.id} />
                </Provider>
              </TabProyecto.BodyItem>
              <TabProyecto.BodyItem id={2}>
                <Provider store={ this.state.store_matriz }>
                  <MatrizDeConsistencia proyecto={proyecto.data} />
                </Provider>
              </TabProyecto.BodyItem>
              <TabProyecto.BodyItem id={3}>
                <Reportes proyecto={proyecto.data} />
              </TabProyecto.BodyItem>
            </TabProyecto.Body>
          </TabProyecto>
        </div>
    )
  }
}

const mapStateToProps = state => ({
  auth: state.resource.auth,
  proyecto: state.resource.proyecto
})

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Proyecto)



