import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import { ResourceActions, AuthActions } from '../actions'

import { Glyph } from 'elemental'


const MenuItem = ({ children, activo }) => (
  <div className={ 'item ' + ( activo ? 'activo' : null ) }>
    { children }
  </div>
);

const Logo = () => (
  <Link to={ '/' } className='activo'>
    <Glyph icon='home' />
  </Link>
);

const Avatar = ({ url }) => (
  <img src={ url } className='avatar' />
);


class Navegacion extends Component {
  constructor(props) {
    super(props);
    props.actions.perfil("AUTH");
  }

  render() {
    const { auth } = this.props;

    return (
      !auth ? null :
      <nav id='menu'>
        <div className='izquierda'>
          <Logo />
          <MenuItem>
            <Glyph icon='info' />{' '}
            Quienes Somos
          </MenuItem>
          <MenuItem>
            <Glyph icon='organization'/>{' '}
            Ayuda
          </MenuItem>
        </div>
        <div className='derecha'>
          <MenuItem activo>
            <strong>
              <a style={{color: "#fff"}} href="/auth/logout">Cerrar Sesión</a>
            </strong>
            <Glyph icon='triangle-down' />
          </MenuItem>
          <MenuItem activo>
            <Avatar url={ auth.data.avatar } />
            <Glyph icon='triangle-down' />
          </MenuItem>
          <MenuItem activo>
            { '(6) ' }
            <Glyph icon='bell' />
            <Glyph icon='triangle-down' />
          </MenuItem>
        </div>
      </nav>
    );
  }
}

const mapStateToProps = state => ({
  auth: state.resource.auth
})

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators({ ...ResourceActions, ...AuthActions }, dispatch)
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Navegacion)



