import React, { Component } from 'react'
import PropTypes from 'prop-types'
// Elemental y Static
import { Row, Col } from 'elemental'
import bg_tab from '../static/tab_body_bg.png'


const TabHeader = ({ children }) => (
  <Col sm="1" style={{
      display: 'flex',
      height: 48,
      margin: '0 168px',
    }}>
    { children }
  </Col>
);


const TabLink = ({ children, activo, id, onClickLink }) => {
  let extra = {}
  let stylos = {
      alignItems: 'center',
      color: '#fff',
      display: 'flex',
      fontWeight: 'bold',
      padding: '0 22px',
      cursor: 'pointer'
    };

  if (activo) {
    extra = {
      background: `url(${bg_tab}) top center`,
      borderRadius: '3px 3px 0 0',
      boxShadow: '0px 5px 0px 0px #ff5722 inset',
      color: '#000',
      marginBottom: -5,
      zIndex: 1
    }
  }

  return (
    <div style={{ ...stylos, ...extra }} onClick={ () => onClickLink(id) }>
      { children }
    </div>
  )
}


const TabBody = ({ children }) => (
  <Col sm="1"
    style={{
      background: '#fff',
      borderTop: '5px solid #ff5722',
      minHeight: 280,
      background: `url(${bg_tab}) top center`
    }}>
    { children }
  </Col>
)


const TabBodyItem = ({ children, activo }) => {
  let extra = {}
  let stylos = {
      display: 'none',
      margin: '24px 168px'
    };

  if (activo) {
    extra = { display: 'flex' }
  }

  return (
    <div style={{ ...stylos, ...extra }}>
      { children }
    </div>
  )
}


class TabProyecto extends Component {
  static propTypes = {
    children: PropTypes.array.isRequired
  }

  constructor(props) {
    super(props);
    const seleccionado = props.default;

    this.state = { seleccionado: seleccionado ? seleccionado: 1 }
    this.onClickLink = this.onClickLink.bind(this);
  }

  onClickLink(id) {
    this.setState({ seleccionado: id })
  }

  render() {
    const children = this.props.children;
    const seleccionado = this.state.seleccionado;

    return (
      <Row style={{ margin: 0 }}>
        <TabHeader>
          {children.map(componente => {
            if (componente.type === TabHeader) {
              return componente.props.children.map(link =>
                link.type === TabLink ?
                  <TabLink {...link.props}
                    activo={seleccionado === link.props.id ? true : false}
                    onClickLink={this.onClickLink} />
                : null
              )
            }
          })}
        </TabHeader>
        <TabBody>
          {children.map(componente => {
            if (componente.type === TabBody) {
              return componente.props.children.map(item =>
                item.type === TabBodyItem ?
                  <TabBodyItem {...item.props}
                    activo={seleccionado === item.props.id ? true : false} />
                : null
              )
            }
          })}
        </TabBody>
      </Row>
    )
  }
}


TabProyecto.Header = TabHeader;
TabProyecto.Body = TabBody;
TabProyecto.Link = TabLink;
TabProyecto.BodyItem = TabBodyItem;

export default TabProyecto;



