import React, { Component } from 'react'
import { applyMiddleware, bindActionCreators, createStore } from 'redux'
import { connect, Provider  } from 'react-redux'
import { ResourceActions } from '../actions'
import { Redirect } from 'react-router'
// middleware
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import promiseMiddleware from 'redux-promise-middleware'
// Componentes
import { resourceReducer } from '../reducers'
import { ListaProyectoForm } from '../components'
import { Row, Col, Card, Form, FormField, FormInput, Button } from 'elemental'


class ProyectoNuevo extends Component {
  constructor(props){
    super(props);

    let store_proyecto = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    )

    this.state = {
      store_proyecto: store_proyecto
    };

    this.onGuardar = this.onGuardar.bind(this);
  }

  onGuardar(event) {
    event.preventDefault();
    const { actions } = this.props;
    const nombre = document.getElementById("nombre").value;
    const descripcion = document.getElementById("descripcion").value;

    actions.crear("PROYECTO", {nombre: nombre, descripcion: descripcion});
  }

  render() {
    const { auth, proyecto, actions } = this.props;

    if (proyecto !== undefined){
      if(proyecto.created){
        actions.clear("PROYECTO");
        return <Redirect to={"/proyecto/" + proyecto.data.id}/>;
      }
    }

    return (
      <Row style={{ margin: '32px 168px' }} >
        <Col sm="60%">
          <Card>
            <h2>Crear Proyecto</h2>
            <Form onSubmit={this.onGuardar}>
	            <FormField label="Nombre del Proyecto" htmlFor="nombre">
		            <FormInput
		              autoFocus
		              id="nombre"
		              name="nombre"
		              placeholder="Ingrese el nombre del proyecto"
		            />
	            </FormField>
	            <FormField label="Descripción" htmlFor="descripcion">
		            <FormInput
		              multiline
		              id="descripcion"
		              name="descripcion"
		            />
	            </FormField>
	            <Button type="primary" submit>Crear</Button>
            </Form>
          </Card>
        </Col>
        <Col sm="40%">
          <Provider store={this.state.store_proyecto}>
            <ListaProyectoForm usuario={auth} />
          </Provider>
        </Col>
      </Row>
    )
  }
}

const mapStateToProps = state => ({
  proyecto: state.resource.proyecto
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProyectoNuevo)



