import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { ResourceActions, RequisitosActions, TareaActions } from '../actions'
import { Row, Col, Card } from 'elemental'


class HistorialUsuario extends Component {

  componentDidMount() {
    const { actions } = this.props;
    actions.listar("HISTORIAL_USUARIO", {id:null});
  }

  render() {
    const { historial, usuario } = this.props

    if (historial === undefined) {
      return <div></div>;
    }
    if (Object.keys(historial.data).length === 0) {
      return <div></div>;
    }

    console.log(historial);

    return (
      <Card>
        <h2>Historial del usuario</h2>
        {historial.data.map((item, key)=>
          <p key={key}>
            <a>{usuario.data.username}</a> -
             actulizo - <a>{item.object_repr}</a> el {" "}
            {(new Date(item.action_time)).toGMTString('en-US')}
          </p>
        )}
      </Card>
    )
  }
}

const mapStateToProps = state => ({
  historial: state.resource.historial
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(HistorialUsuario)



