import { RequisitosApi, ResourceApi } from '../data_managers'
import configuracion from '../data_managers/configuracion'
import axios from 'axios';


export function listarTarea(proyecto, RU, RS) {
  return dispatch => {
    return dispatch({
      type: "TAREA:GET",
      payload: axios({
        ...configuracion,
        method: "GET",
        url: `/web-api/projects/${proyecto.id}/requisito-usuario/${RU.id}/${RS.id}/tarea/`
      })
    });
  };
}


export function detalleTarea(proyecto, RU, RS, id) {
  return dispatch => {
    return dispatch({
      type: "TAREA:GET",
      payload: axios({
        ...configuracion,
        method: "GET",
        url: `/web-api/projects/${proyecto.id}/requisito-usuario/${RU.id}/${RS.id}/tarea/${id}/`
      })
    });
  };
}


export function crearTarea(proyecto, RU, RS, data) {
  return dispatch => {
    return dispatch({
      type: "TAREA:POST",
      payload: axios({
        ...configuracion,
        method: "POST",
        url: `/web-api/projects/${proyecto.id}/requisito-usuario/${RU.id}/${RS.id}/tarea/`,
        data: data
      })
    });
  };
}


export function actualizarTarea(proyecto, RU, RS, id, data) {
  return dispatch => {
    return dispatch({
      type: "TAREA:PUT",
      payload: axios({
        ...configuracion,
        method: "PUT",
        url: `/web-api/projects/${proyecto.id}/requisito-usuario/${RU.id}/${RS.id}/tarea/${id}/`,
        data: data
      })
    });
  };
}


export function eliminarTarea(proyecto, RU, RS, id) {
  return dispatch => {
    return dispatch({
      type: "TAREA:DELETE",
      payload: axios({
        ...configuracion,
        method: "DELETE",
        url: `/web-api/projects/${proyecto.id}/requisito-usuario/${RU.id}/${RS.id}/tarea/${id}/`
      })
    });
  };
}

export function listarTodasLasTareas(proyecto) {
  return dispatch => {
    return dispatch({
      type: "TAREA:GET",
      payload: axios({
        ...configuracion,
        method: "GET",
        url: `/web-api/projects/${proyecto.id}/tareas/`
      })
    });
  };
}

export function resetTarea() {
  return { type: "TAREA:RESET" };
}



