import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { applyMiddleware, bindActionCreators, createStore } from 'redux'
import { connect, Provider } from 'react-redux'
import { ResourceActions } from '../actions'
import { resourceReducer } from '../reducers'
import { Route } from 'react-router-dom'
// middleware
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import promiseMiddleware from 'redux-promise-middleware'
// Elemental
import { Row, Col } from 'elemental'
// Componentes
import { ProyectoNuevo, Proyecto } from '.'
import { ListaProyectoForm, HistorialUsuario } from '../components'


class MainSection extends Component {
  static propTypes = {
    auth: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired
  }

  constructor(props) {
    super(props);

    let store_proyecto = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    )

    let store_historial = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    )

    this.state = {
      store_proyecto: store_proyecto,
      store_historial: store_historial
    };
  }

  render() {
    let { store_proyecto, store_historial } = this.state;
    const { auth } = this.props;

    return (
      auth.fetching ? null :
      <Row style={{ margin: '32px 168px' }} >
        <Col sm="60%">
          <Provider store={store_historial}>
            <HistorialUsuario usuario={auth} />
          </Provider>
        </Col>
        <Col sm="40%">
          <Provider store={store_proyecto}>
            <ListaProyectoForm usuario={auth} />
          </Provider>
        </Col>
      </Row>
    )
  }
}

const mapStateToProps = state => ({
  auth: state.resource.auth,
})

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MainSection)



