import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { ResourceActions } from '../actions'
// Elemental
import { Card, FormInput, Button, Row, Col } from 'elemental'
// Estilos
const Stylos = {
  formulario : {
    cabecera : {
      alignItems: 'center',
      display: 'flex',
      margin: '8px 0',
    },
    filtros: {
      alignItems: 'baseline',
      display: 'flex',
      justifyContent: 'space-around',
      margin: '8px 0',
      padding: '0 8px',
      width: '100%',
    },
    filtrosItem: {
      listStyle: 'none',
      cursor: 'pointer',
    },
    items: {
      borderTop: '1px solid rgba(158, 158, 158, 0.7)',
      fontWeight: 'bold',
      margin: '0',
      padding: '8px 16px'
    }
  },
}

class ListaProyectoForm extends Component {
  static propTypes = {
    usuario: PropTypes.object.isRequired,
  }

  constructor(props) {
    super(props);

    this.state = {
      texto_busqueda: "",
      busquedaTimer: null,
      proyectos: []
    };
    this.realizarFiltro = this.realizarFiltro.bind(this);
    this.busquedaProyecto = this.busquedaProyecto.bind(this);
    this.onChangeBusquedaTexto = this.onChangeBusquedaTexto.bind(this);
  }

  realizarFiltro(event) {
    document.getElementById("filtros")
      .childNodes.forEach( filtro => {
        filtro.style.boxShadow = "none";
      });
    event.target.style.boxShadow = "0 5px 0 0 #FF5722";
  }

  busquedaProyecto() {
    // Llamos el método para realizar la búsqueda
    let { proyectos, texto_busqueda } = this.state;
    if (texto_busqueda === ""){
      this.setState({
        proyectos: this.props.proyecto.data.results
      });
    }
    else
    {
      this.setState({
        proyectos: proyectos.filter(
          item => item.nombre.toLowerCase().startsWith(texto_busqueda)
        )
      });
    }
  }

  onChangeBusquedaTexto(event) {
    // Cada vez que el usuario ingresa una letra se agrega un retardo
    // de 600 ms para realizar la búsqueda

    this.setState((prevState, props) => ({
      busquedaTimer: clearTimeout(prevState.busquedaTimer)
    }));

    this.setState({
      texto_busqueda: event.target.value,
      busquedaTimer: setTimeout(
        this.busquedaProyecto,
        600
      )
    });
  }

  componentDidMount() {
    const { usuario, actions } = this.props;
    actions.listar("PROYECTO", usuario.data);
  }

  componentWillReceiveProps(nextProps, prevState) {
    if (Object.keys(nextProps.proyecto).length !== 0) {
      this.setState({
        proyectos: nextProps.proyecto.data.results
      });
    }
  }

  render() {
    if ( this.props.proyecto === undefined ) return <div></div>;

    let proyectos = this.state.proyectos;
    let { fetching, fetched } = this.props.proyecto;

    return (
      <Card>
        <Row>
          {/* Cabecera */}
          <Col sm="1">
            <Row>
              <Col sm="1/2" style={ Stylos.formulario.cabecera }>
                <i className="fa fa-cubes" aria-hidden="true"
                  style={{ marginRight: 4 }}></i>
                Tus Proyectos
              </Col>
              <Col sm="1/2" style={ Stylos.formulario.cabecera }>
                {/* Botón Agregar nuevo */}
                <Link to={ "/proyecto/nuevo" }>
                  <Button size="sm" type="success">
                    <i className="fa fa-cube" aria-hidden="true"
                      style={{ marginRight: 4 }}></i>
                    <strong>Nuevo Proyecto</strong>
                  </Button>
                </Link>
              </Col>
              <Col sm="1" style={ Stylos.formulario.cabecera }>
                {/* Input Búsqueda */}
                <FormInput size="sm" placeholder="Buscar proyecto"
                  value={this.state.texto_busqueda}
                  onChange={ this.onChangeBusquedaTexto } />
              </Col>
              <Col sm="1" style={ Stylos.formulario.cabecera }>
                {/* Filtros */}
                <ul style={ Stylos.formulario.filtros } id="filtros">
                  <li style={{
                      ...Stylos.formulario.filtrosItem,
                      boxShadow: "0 5px 0 0 #FF5722"
                    }}
                    onClick={ this.realizarFiltro }>
                    Todos
                  </li>
                  <li style={ Stylos.formulario.filtrosItem }
                    onClick={ this.realizarFiltro }>
                    Propios
                  </li>
                  <li style={ Stylos.formulario.filtrosItem }
                    onClick={ this.realizarFiltro }>
                    Terceros
                  </li>
                </ul>
              </Col>
            </Row>
          </Col>
          {/* Contenido */}
          <Col sm="1">
            {fetching ?
              <Row>
                <Col style={ Stylos.formulario.items }>
                  Cargando...
                </Col>
              </Row>
            :
              <Row>
                {(proyectos.length === 0) ?
                  <Col style={ Stylos.formulario.items }>
                    Sin resultaos...
                  </Col>
                : proyectos.map(proyecto => (
                  <Col sm="1" key={ proyecto.id }
                    style={ Stylos.formulario.items }>
                    <Link to={"/proyecto/" + proyecto.id }
                      value={ proyecto.url }>
                      <i className="fa fa-cube" aria-hidden="true"
                        style={{ marginRight: 8 }}></i>
                      { proyecto.nombre }
                    </Link>
                  </Col>
                ))}
              </Row>
            }
          </Col>
        </Row>
      </Card>
    )
  }
}

const mapStateToProps = state => ({
  proyecto: state.resource.proyecto
})

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ListaProyectoForm)



