import { RequisitosApi, ResourceApi } from '../data_managers'
import { configuracion as cnf } from './configuracion'
import configuracion from '../data_managers/configuracion'
import axios from 'axios';


export function listarEquipoTrabajo(resource, proyecto) {
  return dispatch => {
    return dispatch({
      type: resource + ":GET",
      payload: RequisitosApi.listar(
        cnf["PROYECTO"].url + proyecto.id + cnf[resource].url
      )
    });
  };
}


export function crearEquipoTrabajo(resource, proyecto, data) {
  return dispatch => {
    return dispatch({
      type: resource + ":POST",
      payload: ResourceApi.crear(
        data,
        cnf["PROYECTO"].url + proyecto.id + cnf[resource].url
      )
    });
  };
}


export function eliminarEquipoTrabajo(resource, proyecto, id) {
  return dispatch => {
    return dispatch({
      type: resource + ":DELETE",
      payload: axios({
        ...configuracion,
        method: "DELETE",
        url: cnf["PROYECTO"].url + proyecto.id + cnf[resource].url +
        "?usuario=" + id
      })
    });
  };
}



