import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import { EquipoTrabajoActions  } from '../actions'
// Elemental y Static
import { Spinner, Row, Col, Button, ButtonGroup, Glyph } from 'elemental'
import { EquipoTrabajoModal } from '.'


class ProyectoHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false
    };
    this.onCancelModal = this.onCancelModal.bind(this);
  }

  componentDidMount() {
    const { actions, proyecto } = this.props;
    actions.listarEquipoTrabajo("EQUIPO_TRABAJO", proyecto.data);
  }

  onCancelModal() {
    this.setState({
      isOpen: false
    });
  }

  render() {
    const { username, proyecto, equipo_trabajo } = this.props;
    let integrantes = [];

    if (equipo_trabajo !== undefined) {
      if (equipo_trabajo.data.results !== undefined) {
        equipo_trabajo.data.results.forEach(item => {
          const temp = integrantes.filter(usuario =>
            usuario.id === item.usuario.id
          );
          if(temp.length === 0) {
            integrantes.push({
              ...item.usuario,
              roles: [{ id: item.id, rol: item.tipo}]
            });
          }
          else {
            const index = integrantes.findIndex(usuario =>
              usuario.id === item.usuario.id
            );
            integrantes[index].roles.push({id: item.id, rol: item.tipo});
          }
        });
      }
    }

    return (
      <Row style={{ margin: '32px 168px 16px 168px' }}>
        {equipo_trabajo === undefined ? null :
          <EquipoTrabajoModal
            isOpen={this.state.isOpen}
            onCancel={this.onCancelModal}
            integrantes={integrantes}
            proyecto={proyecto.data}
          />
        }
        <Col sm="1/2">
          {/* Información del proyecto */}
          <h1 style={{ color: '#fff', fontSize: 18 }}>
          <Link to={ '/perfil/' + username }
            style={{ color: '#ffeb3b' }}>
            { username }
          </Link>
          { ' / ' + proyecto.data.nombre }
          </h1>
        </Col>
        <Col sm="1/2" style={{ display: 'flex', justifyContent: 'flex-end' }}>
          <ButtonGroup style={{ marginRight: 12 }}>
	          <Button type="primary" size="xs"
	            onClick={() =>{
	              this.setState({
	                isOpen: true
	              });
	            }}
	          >
	            <Glyph icon="organization" />{" "}
	            Equipo de Trabajo
	          </Button>
	          <Button type="primary" size="xs">({ integrantes.length })</Button>
          </ButtonGroup>
        </Col>
      </Row>
    )
  }
}

const mapStateToProps = state => ({
  equipo_trabajo: state.resource.equipo_trabajo
})

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(EquipoTrabajoActions, dispatch)
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProyectoHeader)



