import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { EquipoTrabajoActions } from '../actions'
import PropTypes from 'prop-types'
import { Modal, ModalHeader, ModalBody, ModalFooter, Spinner } from 'elemental'
import { Form, Button, Table, FormField, FormInput, Alert, Pill } from 'elemental'


const Alerta = ({ tipo, contenido }) => (
  <div style={{margin: "12px 16px -18px 16px"}}>
  <Alert type={tipo}>
    <strong><span className="fa fa-comment"/> Mensaje:</strong>
    {" " + contenido}
  </Alert>
  </div>
);

const ROLES_DE_TRABAJO = {
  J: 'Jefe de proyecto',
  A: 'Analista',
  D: 'Diseñador',
  P: 'Programador',
  T: 'Tester',
};

class EquipoTrabajoModal extends Component {
  static propTypes = {
    isOpen: PropTypes.bool.isRequired,
    onCancel: PropTypes.func.isRequired,
    integrantes: PropTypes.array.isRequired
  }

  constructor(props){
    super(props);
    this.state ={
      equipo_trabajo: {},
      actualizar: false,
      crear: false,
      target: {}
    }
    this.onGuardar = this.onGuardar.bind(this);
    this.onSelectRol = this.onSelectRol.bind(this);
    this.onDelete = this.onDelete.bind(this);
  }

  componentWillReceiveProps(nextProps, prevState) {
    if (nextProps.equipo_trabajo) {
      this.setState({ equipo_trabajo: nextProps.equipo_trabajo });
      if (nextProps.equipo_trabajo.creating) {
        this.setState({
          alerta: <Alerta tipo={"success"}
            contenido="Se guardaron los cambios"/>
        });
      }
      if (nextProps.equipo_trabajo.deleted) {
        this.setState({
          alerta: <Alerta tipo={"success"}
            contenido="Se elimino con éxito"/>
        });
        const { actions, proyecto } = this.props;
        actions.listarEquipoTrabajo("EQUIPO_TRABAJO", proyecto);
      }
    }
  }

  onGuardar(event) {
    event.preventDefault();
    const { integrantes, actions, proyecto } = this.props;
    const { crear, target } = this.state;


    if (crear) {
      const reg = /^(\w*\d*(@)*(\.)*(\+)*(-)*(_)*){3,35}$/;

      if ( target.username !== "" && reg.test(target.username)){
        actions.crearEquipoTrabajo("EQUIPO_TRABAJO", proyecto, target);
      }
      else {
        this.setState({
          alerta: <Alerta tipo={"danger"}
            contenido="Ingrese un nombre de usuario correcto"/>
        })
      }
    }
    else {
        let data = integrantes.find(usuario => usuario.id === target.id);
        let arr = [];

        target.roles.forEach(rol => {
          const temp = data.roles.find(rol_ => rol_.rol === rol.rol);
          if(temp){
            arr.push(temp)
          }
          else {
            arr.push(rol)
          }
        });
        data.roles = arr;
        actions.crearEquipoTrabajo("EQUIPO_TRABAJO", proyecto, data);
    }
  }

  onSelectRol(htmlCollection) {
    const { target } = this.state;
    let roles = [];

    Array.from(htmlCollection).forEach(item => {
      if (item.selected) {
        roles.push({ rol: item.value });
      }
    });

    this.setState({
      target: { ...target, roles: roles}
    });
  }

  onDelete(usuario) {
    console.log(usuario);

    if (window.confirm("Eliminar al integrante?")) {
      if (usuario.roles.find(rol => rol.rol === "J")) {
        this.setState({
          alerta: <Alerta tipo={"danger"}
            contenido="No se puede eliminar al Jefe de Proyecto"/>
        });
      }
      else {
        const { integrantes, actions, proyecto } = this.props;
        actions.eliminarEquipoTrabajo("EQUIPO_TRABAJO", proyecto, usuario.id);
      }
    }
  }

  render() {
    const { isOpen, onCancel, integrantes } = this.props;
    const { equipo_trabajo, actualizar, crear, target, alerta } = this.state;

    return (
      <Modal isOpen={isOpen} onKeyDown={(event) => {
          if (event.keyCode === 27) onCancel(); // 27 keyCode (ESQ)
        }}>
        <ModalHeader text="Equipo de Trabajo"
          showCloseButton onClose={onCancel}>
          { alerta }
	      </ModalHeader>
	      <ModalBody>
        {(actualizar || crear) ?
          <Form onSubmit={this.onGuardar} type="horizontal">
            <div>
              <FormField label="Usuario" htmlFor="usuario">
		            <FormInput placeholder="Ingrese el nombre del usuario"
		              name="usuario" id="usuario" disabled={actualizar}
		              value={target.username}
		              onChange={(event) => {
		                this.setState({
		                  target: { ...target, username: event.target.value }
		                })
		              }}
		            />
	            </FormField>
	            <FormField label="Roles del usuario" htmlFor="roles">
		            <select id="roles" multiple
		              style={{ width: "100%", height: 120 }}
		              value={target.roles.map(rol => rol.rol)}
		              onChange={(event) => {
                    this.onSelectRol(event.target.selectedOptions);
		              }}
		            >
		              <option value="J">Jefe de proyecto</option>
                  <option value="A">Analista</option>
                  <option value="D">Diseñador</option>
                  <option value="P">Programador</option>
                  <option value="T">Tester</option>
		            </select>
	            </FormField>
            </div>
            {this.props.equipo_trabajo.creating ? "Cargando..." :
              <div>
                <Button type="primary" submit>
                  { crear ? "Guardar" : actualizar ? "Actulizar" : "Guardar" }
                </Button>
                <Button type="link-cancel"
                  onClick={() =>{
                    this.setState({
                      target: {},
                      crear: false,
                      actualizar: false,
                      alerta: null
                    });
                  }}
                >
                  Cancelar
                </Button>
              </div>
            }
          </Form>
        :
          <Table>
            <thead>
		          <tr>
			          <th><strong>Usuario</strong></th>
			          <th><strong>Rol</strong></th>
			          <th><strong>Acciones</strong></th>
		          </tr>
	          </thead>
	          <tbody>
		        {integrantes.map((usuario, key) =>
              <tr key={key}>
                <td>
                  <a href={ '/perfil/' + usuario.username }>
                    { usuario.username }
                  </a>
                </td>
                <td>
                {usuario.roles.map((rol, key) =>
                  <Pill key={key} label={ROLES_DE_TRABAJO[rol.rol]} type="primary"/>
                )}
                </td>
                <td>
                  <Button size="xs" type="warning"
                    onClick={() =>{
                      this.setState({
                        actualizar: true,
                        target: usuario
                      });
                    }}
                  >
		                <span className="fa fa-edit"/>
		              </Button>{" "}
		              <Button size="xs" type="danger"
		                onClick={() => {
		                  this.onDelete(usuario);
		                }}
		              >
		                <span className="fa fa-trash"/>
		              </Button>
                </td>
              </tr>
		        )}
		        <tr>
		          <td colSpan="3" style={{ textAlign: "center" }}>
		            <Button size="xs" type="success"
		              onClick={() =>{
                    this.setState({
                      crear: true,
                      target: { id: null, roles: [], username: "" },
                      alerta: null
                    });
                  }}
		            >
		              <span className="fa fa-plus-circle"/>{" "}
		              Agregar usuario
		            </Button>
		          </td>
		        </tr>
	          </tbody>
          </Table>
        }
	      </ModalBody>
      </Modal>
    );
  }
}

const mapStateToProps = state => ({
  equipo_trabajo: state.resource.equipo_trabajo
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(EquipoTrabajoActions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(EquipoTrabajoModal)



