import { ResourceApi } from '../data_managers'
import { configuracion } from './configuracion'


export function listar(resource, objeto=null, pagina=1) {
  return dispatch => {
    return dispatch({
      type: resource + ":GET",
      payload: ResourceApi.listar(
        objeto,
        { ...configuracion[resource], pagina: pagina }
      )
    });
  };
}


export function detalle(resource, id) {
  return dispatch => {
    return dispatch({
      type: resource + ":GET",
      payload: ResourceApi.detalle(id, configuracion[resource].url)
    });
  };
}


export function crear(resource, data) {
  return dispatch => {
    return dispatch({
      type: resource + ":POST",
      payload: ResourceApi.crear(data, configuracion[resource].url)
    });
  };
}


export function actualizar(resource, data, method="PUT") {
  return dispatch => {
    return dispatch({
      type: resource + ":" + method,
      payload: ResourceApi.actualizar(
        data,
        configuracion[resource].url,
        method
      )
    });
  };
}


export function eliminar(resource, id) {
  return dispatch => {
    return dispatch({
      type: resource + ":DELETE",
      payload: ResourceApi.eliminar(id, configuracion[resource].url)
    });
  };
}


export function clear(resource) {
  return { type: resource + ":CLEAR" }
}



