import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { ResourceActions } from '../actions'
// Componentes
import { Row, Col, Card } from 'elemental'
import { ProyectoDetalleForm } from '.'


class ProyectoInfo extends Component {

  constructor(props) {
    super(props);
    const { actions, id } = props;

    actions.detalle("PROYECTO", id);
  }

  render() {
    const { id } = this.props;

    return (
      <Row>
        <Col sm="60%">
          <ProyectoDetalleForm id={id} />
        </Col>
        <Col sm="40%">
          <Card>
            Algo
          </Card>
        </Col>
      </Row>
    )
  }
}

const mapStateToProps = state => ({
  proyecto: state.resource.proyecto
});


const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
});


export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProyectoInfo)



