import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { ResourceActions, RequisitosActions } from '../actions'
import PropTypes from 'prop-types'
import { Modal, ModalHeader, ModalBody, ModalFooter, Spinner } from 'elemental'
import { Form, Button, FormField, FormInput, Alert, FormNote } from 'elemental'


const Alerta = ({ tipo, contenido }) => (
  <div style={{margin: "12px 16px -18px 16px"}}>
  <Alert type={tipo}>
    <strong><span className="fa fa-comment"/> Mensaje:</strong>
    {" " + contenido}
  </Alert>
  </div>
);


const initialData = {
  id: null,
  url: null,
  nombre: "",
  descripcion: ""
}

class RequisitoModal extends Component {
  static propTypes = {
    isOpen: PropTypes.bool.isRequired,
    query: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    onCancel: PropTypes.func.isRequired,
    proyecto: PropTypes.object.isRequired,
    requisitos: PropTypes.object.isRequired
  }

  constructor(props) {
    super(props);
    this.state = {
      data: {
        ...initialData,
        proyecto: props.proyecto.url,
      },
      alerta: null
    };
    this.onGuardar = this.onGuardar.bind(this);
    this.onEliminar = this.onEliminar.bind(this);
  }

  componentWillReceiveProps(nextProps, prevState) {
    const { requisitos, query, proyecto } = nextProps;
    const { mensaje_crear, mensaje_actulizar, mensaje_error } = this.props;

    if (query.actualizar && !requisitos.updating) {
      const data = requisitos.data.results[query.index];
      this.setState({
        data: { ...data },
        alerta: null
      });
    }

    if (query.crear) {
      this.setState({
        data: { ...initialData, proyecto: proyecto.url },
        alerta: null
      });
    }

    if (query.alerta_add) {
      this.setState({
        alerta: <Alerta tipo={"success"} contenido={mensaje_crear}/>
      });
    }

    if (query.alerta_update) {
      this.setState({
        alerta: <Alerta tipo={"success"} contenido={mensaje_actulizar}/>
      });
    }
  }

  onGuardar(event) {
    event.preventDefault()

    const { actions, query, api_model, proyecto} = this.props;
    const { data } = this.state;

    if (data.nombre !== "") {
      if (!data.id) {
        actions.crearRequisito(api_model, proyecto, data);
      }
      if (query.actualizar || data.url) {
        actions.actualizarRequisito(api_model, proyecto, data);
      }
    } else {
      this.setState({
        alerta: <Alerta tipo={"danger"} contenido="verifique los campos"/>
      })
    }
  }

  onEliminar() {
    if (window.confirm("Está seguro que desea eliminar")) {
      const { data } = this.state;
      const { actions, api_model, proyecto } = this.props;
      actions.eliminarRequisito(api_model, proyecto, data.id);
    }
  }

  render() {
    const { isOpen, onCancel, requisitos, query, titulo } = this.props;
    let { data, alerta } = this.state;

    return (
      <Modal isOpen={isOpen} onKeyDown={(event) => {
          if (event.keyCode === 27) onCancel(); // 27 keyCode (ESQ)
        }}>
        <ModalHeader text={titulo}
          showCloseButton onClose={onCancel}>
	        { alerta }
	      </ModalHeader>
	      <ModalBody>
          <Form onSubmit={this.onGuardar}>
	          <FormField label="Nombre"
	            htmlFor="nombre">
		          <FormInput autoFocus name="nombre" id="nombre" required
		            value={data.nombre} autoComplete="off"
		            onChange={(event) => {
		              this.setState({
		                data: { ...data, nombre: event.target.value }
		              });
		            }}
		          />
		          <FormNote>Este campo es requerido</FormNote>
	          </FormField>
	          <FormField label="Descripción"
	            htmlFor="descripcion">
		          <FormInput multiline name="descripcion" id="descripcion"
		            value={data.descripcion}
		            onChange={(event) => {
		              this.setState({
		                data: { ...data, descripcion: event.target.value }
		              });
		            }}
		          />
	          </FormField>
          </Form>
	      </ModalBody>
		      {requisitos.creating ?
		        <ModalFooter>
		          <Button type="primary"><Spinner type="inverted" />
		            Guardando
		          </Button>
		        </ModalFooter>
		      : requisitos.updating ?
		        <ModalFooter>
		          <Button type="primary"><Spinner type="inverted" />
		            Actulizando
		          </Button>
		        </ModalFooter>
	        : <ModalFooter style={{display:"flex", justifyContent:"space-between"}}>
	            <div>
		            <Button type="primary" onClick={this.onGuardar}>
		              { query.crear ? "Guardar" : "Actulizar" }
		            </Button>
		            <Button type="link-cancel" onClick={onCancel}>Cancelar</Button>
		          </div>
		          {data.id ?
		            requisitos.deleting ?
		              <Button type="danger"><Spinner type="inverted" />
		                Eliminando
		              </Button>
		            :
		              <Button type="danger" onClick={this.onEliminar}>
		                Eliminar
		              </Button>
		              : null
		          }
	          </ModalFooter>
		      }
      </Modal>
    );
  }
}

const mapStateToProps = state => ({});
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(
    {...ResourceActions, ...RequisitosActions},
    dispatch
  )
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RequisitoModal)



