import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { EquipoTrabajoActions } from '../actions'
import { Row, Col, Card } from 'elemental'


const Stylos = {
  titulo: {
    textAlign: "center"
  },
  table: {
    border: "1px solid black",
    borderCollapse: "collapse",
    width: "100%"
  },
  celda: {
    border: "1px solid black"
  }
}

const ROLES_DE_TRABAJO = {
  J: 'Jefe de proyecto',
  A: 'Analista',
  D: 'Diseñador',
  P: 'Programador',
  T: 'Tester',
};

class ReporteEquipoTrabajo extends Component {

  componentDidMount() {
    const { actions, proyecto } = this.props;
    actions.listarEquipoTrabajo("EQUIPO_TRABAJO", proyecto);
  }

  render() {
    const { equipo_trabajo } = this.props;
    let integrantes = [];

    if (equipo_trabajo === undefined) {
      return <div></div>;
    }
    if (equipo_trabajo.data.results !== undefined) {
      equipo_trabajo.data.results.forEach(item => {
        const temp = integrantes.filter(usuario =>
          usuario.id === item.usuario.id
        );
        if(temp.length === 0) {
          integrantes.push({
            ...item.usuario,
            roles: [{ id: item.id, rol: item.tipo}]
          });
        }
        else {
          const index = integrantes.findIndex(usuario =>
            usuario.id === item.usuario.id
          );
          integrantes[index].roles.push({id: item.id, rol: item.tipo});
        }
      });
    }

    return (
      <div>
        <h1 style={Stylos.titulo}>
          Reporte Equipo de Trabajo
        </h1>
        <table style={Stylos.table}>
          <thead>
            <tr>
              <th style={Stylos.celda}>Usuario</th>
              <th style={Stylos.celda}>Cargo</th>
            </tr>
          </thead>
          <tbody>
          {integrantes.map((usuario, key) =>
            <tr key={key}>
              <td style={{...Stylos.celda,paddingLeft: 12}}>
                {usuario.username}
              </td>
              <td style={{...Stylos.celda,paddingLeft: 12}}>
                {usuario.roles.map((rol, key) =>
                  <p>{ROLES_DE_TRABAJO[rol.rol]}</p>
                )}
              </td>
            </tr>
		      )}
          </tbody>
        </table>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  equipo_trabajo: state.resource.equipo_trabajo
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(EquipoTrabajoActions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ReporteEquipoTrabajo)



