import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { ResourceActions, RequisitosActions, TareaActions } from '../actions'
import { Row, Col, Card } from 'elemental'


const Stylos = {
  titulo: {
    textAlign: "center"
  },
  table: {
    border: "1px solid black",
    borderCollapse: "collapse",
    width: "100%"
  },
  celda: {
    border: "1px solid black"
  }
}


class ReporteAvanceProyecto extends Component {

  componentDidMount() {
    const { actions, proyecto } = this.props;
    actions.listarRequisito("REQUISITO_USUARIO", proyecto);
    actions.listarRequisito("REQUISITO_SOFTWARE", proyecto);
    actions.listarTodasLasTareas(proyecto);
  }

  calcularAVG(arr) {
    let result = 0;
    arr.forEach(item => {
      result += item.avance;
    });
    return result / arr.length;
  }

  render() {
    const { requisitos_usuario, requisitos_software, tareas } = this.props

    if (requisitos_usuario === undefined ||
        requisitos_software === undefined ||
        tareas === undefined
        ) {
      return <div></div>;
    }
    if (requisitos_usuario.data.results === undefined ||
        requisitos_software.data.results === undefined ||
        tareas === undefined ) {
      return <div></div>;
    }

    return (
      <div>
        <h1 style={Stylos.titulo}>
          Reporte Avance Proyecto
        </h1>
        {/* Tabla para RU */}
        <h2>1. Requisitos de Usuario</h2>
        <table style={Stylos.table}>
          <thead>
            <tr>
              <th style={Stylos.celda}>Nro</th>
              <th style={Stylos.celda}>Nombre</th>
              <th style={Stylos.celda}>Descripción</th>
            </tr>
          </thead>
          <tbody>
          {requisitos_usuario.data.results.map((RU, key) =>
            <tr key={key}>
              <td style={Stylos.celda}>{"RU" + key}</td>
              <td style={Stylos.celda}>{RU.nombre}</td>
              <td style={Stylos.celda}>{RU.descripcion}</td>
            </tr>
          )}
          </tbody>
        </table>
        {/* Tabla para RS */}
        <h2>2. Requisitos de Software</h2>
        <table style={Stylos.table}>
          <thead>
            <tr>
              <th style={Stylos.celda}>Nro</th>
              <th style={Stylos.celda}>Nombre</th>
              <th style={Stylos.celda}>Descripción</th>
            </tr>
          </thead>
          <tbody>
            {requisitos_software.data.results.map((RS, key) =>
              <tr key={key}>
                <td style={Stylos.celda}>{"RS" + key}</td>
                <td style={Stylos.celda}>{RS.nombre}</td>
                <td style={Stylos.celda}>{RS.descripcion}</td>
              </tr>
            )}
          </tbody>
        </table>
        {/* Tabla para Cruce Matriz */}
        <h2>3. Cruce Matriz</h2>
        <table style={Stylos.table}>
          <thead>
            <tr>
              <th style={Stylos.celda}>RU\SR</th>
              {requisitos_software.data.results.map((item, key) =>
			          <th key={key} style={Stylos.celda}>
			            {"RS" + key}
			          </th>
			        )}
            </tr>
          </thead>
          <tbody>
            {requisitos_usuario.data.results.map((item, key) =>
		          <tr key={key}>
			          <td style={Stylos.celda}>
			            {"UR" + key}
			          </td>
			          {requisitos_software.data.results.map((item_, key_) =>
                  <td key={key_} style={{...Stylos.celda, textAlign: "center"}}>
                  {item.cruce_matriz.find(id => item_.id === id)
                   !== undefined ? "X" : ""
                  }
                  </td>
			          )}
		          </tr>
            )}
          </tbody>
        </table>
        {/* Tareas */}
        <h2>4. Tareas Asignadas</h2>
        <table style={Stylos.table}>
          <thead>
            <tr>
              <th style={Stylos.celda}>Nro</th>
              <th style={Stylos.celda}>Nombre</th>
              <th style={Stylos.celda}>Avance (%)</th>
            </tr>
          </thead>
          <tbody>
            {tareas.data.results.map((tarea, key) =>
              <tr key={key}>
                <td style={Stylos.celda}>{key}</td>
                <td style={Stylos.celda}>{tarea.nombre}</td>
                <td style={Stylos.celda}>{tarea.avance}</td>
              </tr>
            )}
            <tr>
              <td colSpan="2" style={Stylos.celda}>
                <strong>Avance Total del Proyecto</strong>
              </td>
              <td style={Stylos.celda}>
                <strong>
                { this.calcularAVG(tareas.data.results) }
                </strong>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  requisitos_usuario: state.resource.requisitos_usuario,
  requisitos_software: state.resource.requisitos_software,
  tareas: state.resource.tareas
});

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(
    {...ResourceActions, ...RequisitosActions, ...TareaActions},
    dispatch
  )
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ReporteAvanceProyecto)



