import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { ResourceActions } from '../actions'
// Elemental y Static
import {
  Form,
  FormField,
  FormIconField,
  FormInput,
  Card,
  Checkbox,
  Button,
  FormRow,
  Alert,
  InputGroup,
  Spinner
} from 'elemental'


class PerfilDetalleForm extends Component {
  constructor(props) {
    super(props)
    const { usuario } = props;

    this.state = {
      data: usuario.data,
      // Otros
      alertas: [],
      actualizado: false,
      es_actulizar: false,
      imgBase64: false
    };
    this.onCancel = this.onCancel.bind(this);
    this.onGuardar = this.onGuardar.bind(this);
    this.onAlertMessage = this.onAlertMessage.bind(this);
    this.onChangeImage = this.onChangeImage.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    const { updated } = nextProps.usuario;
    let { alertas, actualizado } = this.state;

    if (updated && actualizado) {
      alertas.push({
        type: "success",
        mensaje: <span>
          <strong>
            <i className="fa fa-comment"/> Mensaje:
          </strong>
          {' Se actualizó con éxito'}
        </span>
      });
      this.setState({
        imgBase64: false
      });
    }

    this.setState({
      data: nextProps.usuario.data,
      alertas: alertas
    });
  }

  onGuardar(event) {
    event.preventDefault();
    if (this.state.es_actulizar) {
      const { actions } = this.props;

      this.setState((prevState, props) => ({
        actualizado: true,
        es_actulizar: !prevState.es_actulizar
      }));

      let data = this.state.data;

      if (!this.state.imgBase64) {
        delete data.avatar;
      }

      actions.actualizar("USUARIO", data);
    }
  }

  onCancel() {
    let { alertas } = this.state;
    alertas.push({
      type: "info",
      mensaje: <span>
        <strong>
          <i className="fa fa-comment"/> Mensaje:
        </strong>
        {' Se reiniciaron los campos'}
      </span>
    });

    const { data } = this.props.usuario;

    this.setState({
      data: data,
      alertas: alertas,
      es_actulizar: false,
      imgBase64: false
    });
  }

  onAlertMessage(id) {
    setTimeout(() => {
      let alertas = this.state.alertas;
      alertas.splice(id, 1)
      this.setState({ alertas: alertas })
    }, 5000);
  }

  onChangeImage(event) {
    if (event.target.files.length !== 0) {
      let file_read = new FileReader();
      let img_avatar = document.getElementById("img_avatar");
      file_read.readAsDataURL(event.target.files[0]);
      file_read.onload = () => {
        img_avatar.src = file_read.result;
        this.setState({
		      data: {...this.state.data, avatar: file_read.result},
		      es_actulizar: true,
		      imgBase64: true
		    });
      };
    }
  }

  render() {
    const { es_actulizar, alertas } = this.state;
    const { fetching, updating, data } = this.props.usuario;
    let { first_name, last_name, email, avatar } = this.state.data;

    return (
      <Card>
        <Form onSubmit={this.onGuardar}>
          <FormField>
            {alertas.map((alerta, index) => (
              <Alert
                type={alerta.type}
                key={index}
                onLoad={this.onAlertMessage(index)}
              >
              {alerta.mensaje}
              </Alert>
            ))}
          </FormField>
          {(fetching || Object.keys(data).length === 0) ?
            <FormField width="two-fifths" style={{margin: "48px 0 0 40%"}}>
              <i className="fa fa-spinner fa-pulse fa-3x fa-fw"/>
	          </FormField>
          :
          <FormRow>
            <FormField width="one-third"
              style={{
                display: "flex",
                flexDirection: "column"
              }}
            >
              <img id="img_avatar" src={avatar} height="128" width="128"
                style={{
                  border: "1px solid gray",
                  borderRadius: 5,
                  margin: "12px auto",
                  padding: 4,
                }}
              />
              <input id="avatar" type="file"
                style={{
                  margin: 0,
                  padding: "0 42px"
                }}
                accept="image/png"
                onChange={this.onChangeImage}
              />
            </FormField>
            <FormField width="two-thirds">
	            <FormField label="Nombres" htmlFor="nombre">
		            <FormInput
		              id="nombre"
		              value={first_name}
		              disabled={updating}
		              placeholder="Ingrese un nombre"
		              autoComplete="off"
		              name="first_name"
		              onChange={(event) => {
		                this.setState({
		                  data: {...this.state.data, first_name: event.target.value},
		                  es_actulizar: true
		                });
		              }}
		            />
	            </FormField>
	            <FormField label="Apellidos" htmlFor="apellidos">
		            <FormInput
		              id="apellidos"
		              value={last_name}
		              disabled={updating}
		              placeholder="Descripción"
		              name="last_name"
		              onChange={(event) => {
		                this.setState({
		                  data: {...this.state.data, last_name: event.target.value},
		                  es_actulizar: true
		                });
		              }}
		            />
	            </FormField>
	            <FormField label="Correo Electrónico" htmlFor="email">
	              <FormInput
		              id="email"
		              value={email}
		              disabled={updating}
		              placeholder="Email"
		              name="email"
		              onChange={(event) => {
		                this.setState({
		                  data: {...this.state.data, email: event.target.value},
		                  es_actulizar: true
		                });
		              }}
		            />
	            </FormField>
            </FormField>
	          <FormField
	            style={{
	              display: "flex",
	              justifyContent: "space-around",
	              width: "100%"
	            }}
	          >
	            <Button type="warning" onClick={this.onCancel} disabled={!es_actulizar}>
	              <i className="fa fa-ban"/>{" "}
	              Cancelar
	            </Button>
	            { updating ?
	              <Button type="primary">
	                <Spinner type="inverted" />
	                Guardando
	              </Button>
	            :
	              <Button type="primary" submit disabled={!es_actulizar}>
	                <i className="fa fa-save" />{" "}
	                Guardar
	              </Button>
	            }
	          </FormField>
	        </FormRow>
          }
        </Form>
      </Card>
    )
  }
}

const mapStateToProps = state => ({
  usuario: state.resource.usuario
})

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PerfilDetalleForm)



