import { RequisitosApi, ResourceApi } from '../data_managers'
import { configuracion } from './configuracion'


export function listarRequisito(resource, proyecto) {
  return dispatch => {
    return dispatch({
      type: resource + ":GET",
      payload: RequisitosApi.listar(
        configuracion["PROYECTO"].url + proyecto.id + configuracion[resource].url
      )
    });
  };
}


export function detalleRequisito(resource, proyecto, id) {
  return dispatch => {
    return dispatch({
      type: resource + ":GET",
      payload: ResourceApi.detalle(
        configuracion["PROYECTO"].url + proyecto.id +
        configuracion[resource].url + id
      )
    });
  };
}


export function crearRequisito(resource, proyecto, data) {
  return dispatch => {
    return dispatch({
      type: resource + ":POST",
      payload: ResourceApi.crear(
        data,
        configuracion["PROYECTO"].url + proyecto.id + configuracion[resource].url
      )
    });
  };
}


export function actualizarRequisito(resource, proyecto, data, method="PUT") {
  return dispatch => {
    return dispatch({
      type: resource + ":" + method,
      payload: ResourceApi.actualizar(
        data,
        configuracion["PROYECTO"].url + proyecto.id + configuracion[resource].url,
        method
      )
    });
  };
}


export function eliminarRequisito(resource, proyecto, id) {
  return dispatch => {
    return dispatch({
      type: resource + ":DELETE",
      payload: ResourceApi.eliminar(
        id,
        configuracion["PROYECTO"].url + proyecto.id + configuracion[resource].url
      )
    });
  };
}




