import React, { Component } from 'react'
import { applyMiddleware, createStore } from 'redux'
import { Provider } from 'react-redux'
// middleware
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import promiseMiddleware from 'redux-promise-middleware'
// Router
import { BrowserRouter as Router, Route } from 'react-router-dom'
// Dashboard
import { resourceReducer } from '../reducers'
import { Perfil, Proyecto, MainSection, ProyectoNuevo } from '.'
import { Navegacion } from '../components'


export default class Dashboard extends Component {

  constructor(props) {
    super(props);

    let store_auth = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    );

    let store_proyecto = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    );

    this.state = {
      store_auth: store_auth,
      store_proyecto: store_proyecto
    };
  }

  render() {
    const { store_auth } = this.state;

    return (
      <Router>
        <Provider store={this.state.store_auth}>
          <div>
          {/* Navegación */}
            <Navegacion />

          {/* Vistas */}
          {(Object.keys(store_auth).length !== 0) ?
            <div>
              <Route exact path="/" component={MainSection}/>
              <Route path="/proyecto/:id(\d+)" component={Proyecto}/>
              <Route path="/perfil/:username" component={Perfil}/>
              <Route path="/proyecto/nuevo" render={() =>
                <Provider store={this.state.store_proyecto}>
                  <ProyectoNuevo auth={store_auth.getState().resource.auth}/>
                </Provider>
              }/>
            </div>
          :
            <div style={{
                position: 'fixed',
                textAlign: 'center',
                top:'35%',
                width: '100%'
              }}>
              <i className="fa fa-cog fa-spin fa-4x fa-fw"></i>
              <h3>Cargando...</h3>
            </div>
          }
          </div>
        </Provider>
      </Router>
    )
  }
}




