function getCookie(name) {
  var cookieValue = null;
  if (document.cookie && document.cookie !== '') {
    var cookies = document.cookie.split(';');
    for (var i = 0; i < cookies.length; i++) {
      var cookie = cookies[i].trim();
      // Does this cookie string begin with the name we want?
      if (cookie.substring(0, name.length + 1) === (name + '=')) {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}

function getAuthorizationToken() {
  var xhr = new XMLHttpRequest();
  xhr.open('GET', document.location, false);
  xhr.send(null);
  return xhr.getResponseHeader("Authorization");
}

const configuracion = {
  headers: {
    //'Authorization': 'Token fb2469f24c6da5fa291882cd6f55e29c0372bea3',
    //'X-CSRFToken': 'R6t3MJX6C48FGDP7bmJ14uXm3Lp3MXC3GZxnHTxrQozvfbsGPl25d6tq3UW2wjtX'
    'X-CSRFToken': getCookie("csrftoken"),
    'Authorization': getAuthorizationToken()
  }
}

export default configuracion;



