import React, { Component } from 'react'
import { applyMiddleware, bindActionCreators, createStore } from 'redux'
import { connect, Provider } from 'react-redux'
import { ResourceActions } from '../actions'
import { resourceReducer } from '../reducers'
// middleware
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import promiseMiddleware from 'redux-promise-middleware'

import { Row, Col, Card } from 'elemental'
import { ListaProyectoForm, PerfilDetalleForm } from '../components'


class Perfil extends Component {

  constructor(props) {
    super(props);

    let store_proyecto = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    )

    this.state = {
      store_proyecto: store_proyecto
    };
  }

  componentDidMount() {
    const { actions, match } = this.props;
    actions.listar("USUARIO", { id: match.params.username })
  }

  render() {
    let { store_proyecto } = this.state;
    let { usuario } = this.props

    if (usuario === undefined) return <div></div>;
    if (usuario.data === undefined) return <div></div>;

    return (
      usuario.fetching ?
        <div style={{
          position: 'fixed',
          textAlign: 'center',
          top:'35%',
          width: '100%'
        }}>
          <i className="fa fa-cog fa-spin fa-4x fa-fw"></i>
          <h3>Cargando...</h3>
        </div>
      :
        <Row style={{ margin: '32px 168px' }}>
          <Col sm="60%">
            <PerfilDetalleForm />
          </Col>
          <Col sm="40%">
            <Provider store={store_proyecto}>
              <ListaProyectoForm usuario={usuario}/>
            </Provider>
          </Col>
        </Row>
    )
  }
}

const mapStateToProps = state => ({
  auth: state.resource.auth,
  usuario: state.resource.usuario
})

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Perfil)



