import React, { Component } from 'react'
import { bindActionCreators, applyMiddleware, createStore } from 'redux'
import { connect, Provider } from 'react-redux'
import { ResourceActions, RequisitosActions } from '../actions'
// middleware
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import promiseMiddleware from 'redux-promise-middleware'
// Componentes
import { resourceReducer } from '../reducers'
import ReactConfirmAlert, { confirmAlert } from 'react-confirm-alert';
import { Row, Col, Card, Table, Button, Alert, Checkbox } from 'elemental'
import { RequisitoModal, TareaModal } from '.'


const Stylos = {
  matriz: {
    margin: 0,
    overflowX: "scroll",
  },
  altertas: {
    width: 420,
  },
  th: {
    borderBottom: "2px solid #4CAF50",
    height: 112,
    verticalAlign: "middle",
    padding: 0,
    margin: 0
  },
  td: {
    borderTop: "1px solid #4CAF50",
    textAlign: "center",
    padding: 0,
    margin: 0
  },
  cruce: {
    alignItems: "center",
    display: "flex",
    flexDirection: "column",
    verticalAlign: "middle"
  },
  addBotton: {
    borderRadius: "50%",
    height: 38,
    marginTop: 8
  }
};

const Cargando = () => (
  <Row style={{ minWidth: 720, width: "100%" }}>
    <Col sm="100%">
      <Card>
        <h1>Cargando</h1>
      </Card>
    </Col>
  </Row>
);


class MatrizDeConsistencia extends Component {

  constructor(props){
    super(props);

    let store_tarea = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    );

    this.state = {
      isOpenRU: false,
      isOpenRS: false,
      isOpenCM: false,
      isOpenTarea: false,
      target: {},
      query: {},
      store_tarea: store_tarea
    };
    this.onCancelModal = this.onCancelModal.bind(this)
    this.onCancelModalTarea = this.onCancelModalTarea.bind(this)
    this.crearCruceMatrizModal = this.crearCruceMatrizModal.bind(this);
    this.eliminarCruceMatrizModal = this.eliminarCruceMatrizModal.bind(this);
    this.recibePropsRequisitoSoftware = this.recibePropsRequisitoSoftware.bind(this);
  }

  componentDidMount() {
    const { actions, proyecto } = this.props;
    actions.listarRequisito("REQUISITO_USUARIO", proyecto);
    actions.listarRequisito("REQUISITO_SOFTWARE", proyecto);
  }

  componentWillReceiveProps(nextProps, prevState) {
    const { query, isOpenRS, isOpenRU } = this.state;

    // Acciones para el requisito de usuario
    if (isOpenRU) {
      this.recibePropsRequisitoUsuario(nextProps, query);
    }
    // Acciones para el requisito de software
    if (isOpenRS) {
      this.recibePropsRequisitoSoftware(nextProps, query);
    }

  }

  recibePropsRequisitoUsuario(nextProps, query) {
    let { requisitos_usuario, actions, proyecto } = nextProps;

    if (requisitos_usuario.created && query.crear) {
      let index = requisitos_usuario.data.results.length - 1;
      this.setState({
        query: { actualizar: true, index: index, alerta_add: true }
      });
    }
    if (requisitos_usuario.updated && query.actualizar) {
      this.setState({
        query: { ...query, actualizar: true, alerta_update: true }
      });
    }
    if (requisitos_usuario.deleted && query.actualizar) {
      actions.listarRequisito("REQUISITO_USUARIO", proyecto);
      this.onCancelModal();
    }
  }

  recibePropsRequisitoSoftware(nextProps, query) {
    let { requisitos_software, actions, proyecto } = nextProps;

    if (requisitos_software.created && query.crear) {
      let index = requisitos_software.data.results.length - 1;
      this.setState({
        query: { actualizar: true, index: index, alerta_add: true }
      });
    }
    if (requisitos_software.updated && query.actualizar) {
      this.setState({
        query: { ...query, actualizar: true, alerta_update: true }
      });
    }
    if (requisitos_software.deleted && query.actualizar) {
      this.onCancelModal();
      actions.listarRequisito("REQUISITO_SOFTWARE", proyecto);
    }
  }

  crearCruceMatrizModal(requisitos_usuario, requisito_software_id) {
    confirmAlert({
      title: 'Crear cruce matriz',
      message: 'Está segulo que decea realizar el cruce de la matriz?',
      childrenElement: () => <div></div>,
      confirmLabel: 'Aceptar',
      cancelLabel: 'Cancelar',
      onConfirm: () => {
        const { proyecto, actions } = this.props;
        this.setState({ isOpenCM: true, query: { actualizar: true } });
        requisitos_usuario.cruce_matriz.push(requisito_software_id);
        // Actualizamos
        actions.actualizarRequisito(
          "REQUISITO_USUARIO", proyecto, requisitos_usuario
        );
      },
      onCancel: () => {},
    });
  }

  eliminarCruceMatrizModal(requisitos_usuario, requisito_software_id){
    confirmAlert({
      title: 'Eliminar Cruce Matriz',
      message: 'Está segulo que decea eliminar?',
      childrenElement: () =>
        <div style={{ margin: 12 }}>
          <Alert type="danger">
            <span className="fa fa-exclamation-triangle fa-2x"/>{" "}
            Si asepta este eliminará todas las tareas asignadas permanentemente.
          </Alert>
        </div>
      ,
      confirmLabel: 'Aceptar',
      cancelLabel: 'Cancelar',
      onConfirm: () => {
        const { proyecto, actions } = this.props;
        const index = requisitos_usuario.cruce_matriz.findIndex(id =>
          id === requisito_software_id
        );
        requisitos_usuario.cruce_matriz.splice(index,1);
        // Actualizamos
        actions.actualizarRequisito("REQUISITO_USUARIO", proyecto, requisitos_usuario);
      },
      onCancel: () => {},
    });
  }

  onCancelModal() {
    this.setState({
      isOpenRU: false,
      isOpenRS: false,
      isOpenTarea: false
    });
  }

  onCancelModalTarea() {
    const { actions } = this.props;
    this.setState({
      isOpenTarea: false
    });
  }

  render() {
    let { isOpenRU, isOpenRS, isOpenTarea, query, target } = this.state;
    let { actions, proyecto, requisitos_usuario, requisitos_software } = this.props;

    if (typeof(requisitos_usuario) === "undefined" ||
        typeof(requisitos_software) === "undefined") {
      return <Cargando />
    }

    if (!requisitos_usuario.data.results ||
        !requisitos_software.data.results ) {
      return <Cargando />
    }

    return (
      <Row style={{ minWidth: 720, width: "100%" }}>
      {/* Modales */}
      <RequisitoModal
        isOpen={isOpenRU} query={query}
        api_model="REQUISITO_USUARIO"
        proyecto={proyecto}
        requisitos={requisitos_usuario}
        titulo="Requisito de Usuario"
        mensaje_crear="Se creó el Requisito de Usuario"
        mensaje_actulizar="Se actualizo el Requisito de Usuario"
        mensaje_error="Lo sentimos, tenemos algunos problemas :S"
        onCancel={this.onCancelModal}
      />
      <RequisitoModal
        isOpen={isOpenRS} query={query}
        api_model="REQUISITO_SOFTWARE"
        proyecto={proyecto}
        requisitos={requisitos_software}
        titulo="Requisito de Software"
        mensaje_crear="Se creó el Requisito de Software"
        mensaje_actulizar="Se actualizo el Requisito de Software"
        mensaje_error="Lo sentimos, tenemos algunos problemas :S"
        onCancel={this.onCancelModal}
      />
      <Provider store={this.state.store_tarea}>
        <TareaModal
          isOpen={isOpenTarea}
          onCancel={this.onCancelModalTarea}
          proyecto={proyecto}
          target={target}
        />
      </Provider>
      <Col sm="100%">
      <Card>
        <Row style={ Stylos.matriz }>
          <div style={ Stylos.altertas }>
            {/* Alertas */}
          </div>
          <Col>
            <Table style={{ width: "initial" }}>
	            <thead>
		            <tr>
		              <th style={ Stylos.th }></th>
			            {requisitos_software.data.results.map((item, index) =>
			              <th key={ index } style={ Stylos.th }>
			                <Button size="sm" type="success"
			                  style={{ transform: "rotate(-90deg)", width: 90 }}
			                  onClick={() => {
			                    this.setState({
                            isOpenRS: true,
                            query: { actualizar: true, index: index }
                          });
			                  }}>
			                  <span className="fa fa-cog"/>
			                  { " SR" + index }
			                </Button>
			              </th>
			            )}
			            <th style={ Stylos.th }>
			              <Button size="sm" type="success" style={ Stylos.addBotton }
                      onClick={() => {
                        this.setState({
                          isOpenRS: true,
                          query: { crear: true }
                        });
                      }}>
			                <span className="fa fa-plus-circle"/>
			              </Button>
			            </th>
		            </tr>
	            </thead>
	            <tbody>
              {requisitos_usuario.data.results.map((item, index) =>
		            <tr key={ index } style={{ height: 74 }}>
			            <td style={ Stylos.td }>
			              <Button key={index} size="sm" type="success"
			                style={{ width: 90 }}
			                onClick={() => {
			                  this.setState({
                          isOpenRU: true,
                          query: { actualizar: true, index: index }
                        });
			                }}>
			                { "UR" + index + " " }
			                <span className="fa fa-cog"/>
			              </Button>
			            </td>
			            {requisitos_software.data.results.map((item_, index_) =>
                    <td key={ index_ } style={ Stylos.td }>
                    {item.cruce_matriz.find(id => item_.id === id)
                     !== undefined ?
                      <div style={ Stylos.cruce }>
                        <Button type="primary" size="xs"
                          onClick={() =>{
                            this.setState({
                              isOpenTarea: true,
                              target: { RU: item, RS: item_ }
                            });
                          }}
                        >
                          <span className="fa fa-cog"/>
                        </Button>
                        <Checkbox style={{height: 14}} checked readOnly
                          onClick={() =>
                            this.eliminarCruceMatrizModal(item, item_.id)
                          }
                        />
                      </div>
                    : <Checkbox style={{height: 16}} checked={false}
                        onClick={() =>
                          this.crearCruceMatrizModal(item, item_.id)
                        }
                      />
                    }
                    </td>
			            )}
			            <td style={ Stylos.td }></td>
		            </tr>
              )}
              <tr>
                <td style={ Stylos.td }>
                  <Button size="sm" type="success" style={ Stylos.addBotton }
                    onClick={() => {
                      this.setState({
                        isOpenRU: true,
                        query: { crear: true }
                      });
                    }}>
			              <span className="fa fa-plus-circle"/>
			            </Button>
                </td>
                {/* Solo es para el agregar al final la divición */}
                {requisitos_software.data.results.map((item, index) =>
                  <td key={ index } style={ Stylos.td }></td>
			          )}
			          <td style={ Stylos.td }></td>
              </tr>
	          </tbody>
            </Table>
          </Col>
        </Row>
      </Card>
      </Col>
      </Row>
    )
  }
}

const mapStateToProps = state => ({
  requisitos_usuario: state.resource.requisitos_usuario,
  requisitos_software: state.resource.requisitos_software,
  cruce_matriz: state.resource.cruce_matriz
});


const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(
    {...ResourceActions, ...RequisitosActions},
    dispatch
  )
});


export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MatrizDeConsistencia)




