import React, { Component } from 'react'
import { applyMiddleware, createStore } from 'redux'
import { Provider } from 'react-redux'
// middleware
import logger from 'redux-logger'
import thunk from 'redux-thunk'
import promiseMiddleware from 'redux-promise-middleware'
// Componentes
import { resourceReducer } from '../reducers'
import { Row, Col, Card, Button } from 'elemental'
import { ReporteAvanceProyecto, ReporteEquipoTrabajo } from '.'
import axios from 'axios'
import configuracion from '../data_managers/configuracion'


const Cargando = () => (
  <Row style={{ minWidth: 720, width: "100%" }}>
    <Col sm="100%">
      <Card>
        <h1>Cargando</h1>
      </Card>
    </Col>
  </Row>
);

class Controles extends Component {

  imprimir() {
    const contenido = document.getElementById("hoja");
    const mywindow = window.open();
    mywindow.document.write('<html><head><title></title>');
    mywindow.document.write('</head><body><pre>');
    mywindow.document.write(contenido.outerHTML);
    mywindow.document.write('</pre></body></html>');
    mywindow.document.close();
    mywindow.print();
    mywindow.close();
  }

  generarPDF() {
    const contenido = document.getElementById("hoja");
    const html = "<html>"+ contenido.outerHTML +"</html>";

    axios({
      headers: {
        ...configuracion.headers,
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      method: "POST",
      url: "/generar-pdf/",
      data: html
    }).then(response => console.log(response))
  }

  render() {
    return (
      <Row style={{ minWidth: 794, width: "100%" }}>
        <Col
          style={{
            display: "flex",
            justifyContent: "space-between",
            margin: "16px auto",
            maxWidth: 794,
            padding: 0
          }}
        >
          <div>
            <Button type="link" onClick={this.props.onGetBack}>
              <span className="fa fa-arrow-left" />{" "}
              Retroceder
            </Button>
            <Button type="primary" onClick={this.generarPDF}>
              <span className="fa fa-file-pdf-o"/>{" "}
              Exportar PDF
            </Button>
          </div>
          <div>
            <Button onClick={this.imprimir}>
              <span className="fa fa-print"/>{" "}
              Imprimir
            </Button>
          </div>
        </Col>
        <Col>
          <Card style={{ width: 794, margin: "auto" }}>
            <div id="hoja">
              { this.props.children }
            </div>
          </Card>
        </Col>
      </Row>
    )
  }
}


export default class Reportes extends Component {

  constructor(props) {
    super(props);

    let store = createStore(
      resourceReducer,
      applyMiddleware(promiseMiddleware(), thunk, logger)
    );

    this.state = {
      reporte: "N",
      store: store
    }

    this.onGetBack = this.onGetBack.bind(this);
  }

  onGetBack(){
    this.setState({
      reporte: "N"
    });
  }

  render() {
    const { reporte } = this.state;
    const { proyecto } = this.props;

    switch(reporte) {
      case "RP1": {
        return (
          <Controles onGetBack={this.onGetBack}>
            <Provider store={this.state.store}>
              <ReporteAvanceProyecto proyecto={proyecto}/>
            </Provider>
          </Controles>
        )
      }
      case "RP2": {
        return (
          <Controles onGetBack={this.onGetBack}>
            <Provider store={this.state.store}>
              <ReporteEquipoTrabajo proyecto={proyecto}/>
            </Provider>
          </Controles>
        )
      }
      default:
        break;
    }

    return (
      <Row style={{ minWidth: 720, width: "100%" }}>
        <Col>
        <Card>
          <Row style={{ padding: 32 }}>
            <Col>
              <p>Seleccione el reporte</p>
            </Col>
            <Col sm="1/3">
              <Card
                style={{
                  color: "#2196F3",
                  cursor: "pointer",
                  display: "flex",
                  fontSize: 12,
                  justifyContent: "space-evenly",
                  padding: 16,
                }}
                onClick={() => {
                  this.setState({ reporte: "RP1" })
                }}
              >
                <span className="fa fa-cubes fa-2x" />
                <strong style={{ marginTop: 5 }}>
                  Reporte de Avance de Proyecto
                </strong>
              </Card>
            </Col>
            <Col sm="1/3">
              <Card
                style={{
                  color: "#F44336",
                  cursor: "pointer",
                  display: "flex",
                  fontSize: 12,
                  justifyContent: "space-evenly",
                  padding: 16,
                }}
                onClick={() => {
                  this.setState({ reporte: "RP2" })
                }}
              >
                <span className="fa fa-users fa-2x" />
                <strong style={{ marginTop: 5 }}>
                  Reporte de Equipo de Trabajo
                </strong>
              </Card>
            </Col>
            <Col sm="1/3">
              <Card
                style={{
                  color: "#009688",
                  cursor: "pointer",
                  display: "flex",
                  fontSize: 12,
                  justifyContent: "space-evenly",
                  padding: 16,
                }}
              >
                <span className="fa fa-tasks fa-2x" />
                <strong style={{ marginTop: 5 }}>
                  Reporte de Tareas Asignadas
                </strong>
              </Card>
            </Col>
          </Row>
        </Card>
        </Col>
      </Row>
    )
  }
}




