import generic from './generic'


export default function resource(state = {}, action) {
  const [ resource, method ] = action.type.split(":")

  switch (resource) {

    case "AUTH": {
      let action_ = { ...action, type: method };
      return {
        ...state,
        auth: generic(state.usuario, action_)
      }
    }

    case "USUARIO": {
      let action_ = { ...action, type: method };

      if (method === "GET_FULFILLED") {
        let temp = generic(state.usuario, action_);
        console.log(temp);

        return {
          ...state,
          usuario: {
            ...temp,
            data: temp.data.results[0]
          }
        };
      }

      return {
        ...state,
        usuario: generic(state.usuario, action_)
      }
    }

    case "HISTORIAL_USUARIO": {
      let action_ = { ...action, type: method };
      return {
        ...state,
        historial: generic(state.historial, action_)
      }
    }

    case "PROYECTO": {
      let action_ = { ...action, type: method };

      if (method === "CLEAR") {
        return {
          ...state,
          proyecto: {}
        };
      }

      return {
        ...state,
        proyecto: generic(state.proyecto, action_)
      }
    }

    case "EQUIPO_TRABAJO": {
      let action_ = { ...action, type: method };

      return {
        ...state,
        equipo_trabajo: generic(state.equipo_trabajo, action_)
      };
    }

    case "TAREA": {
      let { tareas } = state;
      let action_ = { ...action, type: method };

      if (method === "RESET") {
        return {
          ...state,
          tareas: undefined
        };
      }

      if (method === "POST_FULFILLED") {
        let temp = generic(state.tareas, action_);

        tareas.data.results.push(temp.data);

        return {
          ...state,
          tareas: {
            ...tareas,
            ...temp,
            data: tareas.data
          }
        };
      }

      if (method === "PUT_FULFILLED" || method === "PACTH_FULFILLED") {
        let temp = generic(state.tareas, action_);
        const index = tareas.data
          .results.findIndex(item => item.id === temp.data.id);

        tareas.data.results[index] = temp.data;

        return {
          ...state,
          tareas: {
            ...tareas,
            ...temp,
            data: tareas.data
          }
        }
      }

      return {
        ...state,
        tareas: generic(state.tareas, action_)
      };
    }

    case "REQUISITO_USUARIO": {
      let { requisitos_usuario } = state;
      let action_ = { ...action, type: method };

      if (method === "POST_FULFILLED") {
        let temp = generic(state.requisitos_usuario, action_);

        requisitos_usuario.data.results.push(temp.data);

        return {
          ...state,
          requisitos_usuario: {
            ...requisitos_usuario,
            ...temp,
            data: requisitos_usuario.data
          }
        };
      }

      if (method === "PUT_FULFILLED" || method === "PACTH_FULFILLED") {
        let temp = generic(state.requisitos_usuario, action_);
        const index = requisitos_usuario.data
          .results.findIndex(item => item.id === temp.data.id);

        requisitos_usuario.data.results[index] = temp.data;

        return {
          ...state,
          requisitos_usuario: {
            ...requisitos_usuario,
            ...temp,
            data: requisitos_usuario.data
          }
        }
      }

      return {
        ...state,
        requisitos_usuario: generic(state.requisitos_usuario, action_)
      };
    }

    case "REQUISITO_SOFTWARE": {
      let { requisitos_software } = state;
      let action_ = { ...action, type: method };

      if (method === "POST_FULFILLED") {
        let temp = generic(state.requisitos_software, action_);

        requisitos_software.data.results.push(temp.data);

        return {
          ...state,
          requisitos_software: {
            ...requisitos_software,
            ...temp,
            data: requisitos_software.data
          }
        };
      }

      if (method === "PUT_FULFILLED" || method === "PACTH_FULFILLED") {
        let temp = generic(state.requisitos_software, action_);

        const index = requisitos_software.data
          .results.findIndex(item => item.id === temp.data.id);

        requisitos_software.data.results[index] = temp.data;

        return {
          ...state,
          requisitos_software: {
            ...requisitos_software,
            ...temp,
            data: requisitos_software.data
          }
        }
      }

      return {
        ...state,
        requisitos_software: generic(state.requisitos_software, action_)
      };
    }

    default:
      return state;
  }
}



