import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { ResourceActions } from '../actions'
// Elemental y Static
import {
  Form,
  FormField,
  FormIconField,
  FormInput,
  Card,
  Checkbox,
  Button,
  FormRow,
  Alert,
  InputGroup,
  Spinner
} from 'elemental'


class ProyectoDetalleForm extends Component {
  static propTypes = {
    id: PropTypes.number.isRequired
  }

  constructor(props) {
    super(props)
    const { proyecto } = props;

    this.state = {
      data: proyecto,
      // Otros
      alertas: [],
      actualizado: false,
      es_actulizar: false
    };
    this.onCancel = this.onCancel.bind(this);
    this.onGuardar = this.onGuardar.bind(this);
    this.onAlertMessage = this.onAlertMessage.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    const { updated } = nextProps.proyecto;
    let { alertas, actualizado } = this.state;

    if (updated && actualizado) {
      alertas.push({
        type: "success",
        mensaje: <span>
          <strong>
            <i className="fa fa-comment"/> Mensaje:
          </strong>
          {' Se actualizó con éxito'}
        </span>
      });
    }

    this.setState({
      data: nextProps.proyecto.data,
      alertas: alertas
    });
  }

  onGuardar(event) {
    event.preventDefault();
    if (this.state.es_actulizar) {
      const { actions } = this.props;

      this.setState((prevState, props) => ({
        actualizado: true,
        es_actulizar: !prevState.es_actulizar
      }));

      actions.actualizar("PROYECTO", this.state.data);
    }
  }

  onCancel() {
    let { alertas } = this.state;
    alertas.push({
      type: "info",
      mensaje: <span>
        <strong>
          <i className="fa fa-comment"/> Mensaje:
        </strong>
        {' Se reiniciaron los campos'}
      </span>
    });

    const { data } = this.props.proyecto;

    this.setState({
      data: data,
      alertas: alertas,
      es_actulizar: false
    });
  }

  onAlertMessage(id) {
    setTimeout(() => {
      let alertas = this.state.alertas;
      alertas.splice(id, 1)
      this.setState({ alertas: alertas })
    }, 5000);
  }

  render() {
    const { es_actulizar, alertas } = this.state;
    const { fetching, updating, data } = this.props.proyecto;
    let {
      nombre,
      descripcion,
      fecha_creacion,
      fecha_finalizacion,
      terminado,
    } = this.state.data;

    return (
      <Card>
        <Form type="horizontal" onSubmit={this.onGuardar}>
          <FormField>
            {alertas.map((alerta, index) => (
              <Alert
                type={alerta.type}
                key={index}
                onLoad={this.onAlertMessage(index)}
              >
              {alerta.mensaje}
              </Alert>
            ))}
          </FormField>
          {(fetching || Object.keys(data).length === 0) ?
            <FormField width="two-fifths" style={{margin: "48px 0 0 40%"}}>
              <i className="fa fa-spinner fa-pulse fa-3x fa-fw"/>
	          </FormField>
          :
          <FormRow>
	          <FormField label="Nombre del Proyecto" htmlFor="nombre">
	            <FormIconField iconPosition="left" iconColor="primary" iconKey="package">
		            <FormInput
		              id="nombre"
		              value={nombre}
		              disabled={updating}
		              placeholder="Ingrese un nombre"
		              autoComplete="off"
		              name="nombre"
		              onChange={(event) => {
		                this.setState({
		                  data: {...this.state.data, nombre: event.target.value},
		                  es_actulizar: true
		                });
		              }}
		            />
		          </FormIconField>
	          </FormField>
	          <FormField label="Descripción del Proyecto" htmlFor="descripcion">
		          <FormInput
		            multiline
		            id="descripcion"
		            value={descripcion}
		            disabled={updating}
		            placeholder="Descripción"
		            name="descripcion"
		            onChange={(event) => {
		              this.setState({
		                data: {...this.state.data, descripcion: event.target.value},
		                es_actulizar: true
		              });
		            }}
		          />
	          </FormField>
	          <FormRow>
	            <FormField width="one-third" label="Marcar como concluido">
		            <Checkbox
		              label="Terminado"
		              checked={terminado}
		              disabled={updating}
		              onChange={(event) => {
		                this.setState((prevState, props) => ({
		                  data: {...prevState.data, terminado: !prevState.data.terminado},
		                  es_actulizar: true
		                }));
		              }}
		            />
	            </FormField>
	            <FormField width="one-third" label="Fecha de creación">
	              <InputGroup contiguous>
	                <InputGroup.Section type="primary">
		                <Button type="primary" size="sm">
		                  <span className="fa fa-calendar" />
		                </Button>
	                </InputGroup.Section>
	                <InputGroup.Section grow>
		                <FormInput size="sm" onChange={()=>{}}
		                  value={fecha_creacion} disabled/>
	                </InputGroup.Section>
                </InputGroup>
	            </FormField>
	            <FormField width="one-third" label="Fecha de finalización">
                <InputGroup contiguous>
	                <InputGroup.Section type="primary">
		                <Button type="primary" size="sm">
		                  <span className="fa fa-calendar" />
		                </Button>
	                </InputGroup.Section>
	                <InputGroup.Section grow>
		                <FormInput size="sm" onChange={()=>{}} disabled
		                  value={fecha_finalizacion ? fecha_creacion: "Pendiente"}/>
	                </InputGroup.Section>
                </InputGroup>
	            </FormField>
	          </FormRow>
	          <hr/>
	          <FormField style={{ display: "flex", justifyContent: "space-around" }}>
	            <Button type="warning" onClick={this.onCancel} disabled={!es_actulizar}>
	              <i className="fa fa-ban"/>{" "}
	              Cancelar
	            </Button>
	            { updating ?
	              <Button type="primary">
	                <Spinner type="inverted" />
	                Guardando
	              </Button>
	            :
	              <Button type="primary" submit disabled={!es_actulizar}>
	                <i className="fa fa-save" />{" "}
	                Guardar
	              </Button>
	            }
	          </FormField>
	        </FormRow>
          }
        </Form>
      </Card>
    )
  }
}

const mapStateToProps = state => ({
  proyecto: state.resource.proyecto
})

const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(ResourceActions, dispatch)
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProyectoDetalleForm)



