import axios from 'axios'
import configuracion from './configuracion'


export function perfil(url="/web-api/profile/") {
  return axios({
    ...configuracion,
    method: "GET",
    url: `${url}`
  });
}


export function listar(objeto, configuracion) {
  return axios({
    ...configuracion,
    method: "GET",
    url: `${configuracion.url}?${configuracion.filtro}=${objeto.id}&page=${configuracion.pagina}`
  });
}


export function crear(data, url="/") {
  return axios({
    ...configuracion,
    method: "POST",
    url: `${url}`,
    data: data
  });
}


export function detalle(id, url="/") {
  return axios({
    ...configuracion,
    method: "GET",
    url: `${url}${id}/`
  });
}


export function actualizar(data, url="/", method="PUT") {
  return axios({
    ...configuracion,
    method: method,
    url: `${url}${data.id}/`,
    data: data
  });
}


export function eliminar(id, url="/") {
  return axios({
    ...configuracion,
    method: "DELETE",
    url: `${url}${id}/`
  });
}


export function optionsLista(url="/") {
  return axios({
    ...configuracion,
    method: "OPTIONS",
    url: `${url}`
  });
}


export function optionsDetalle(id, url="/") {
  return axios({
    ...configuracion,
    method: "OPTIONS",
    url: `${url}${id}`
  });
}



